@font-face {
  font-family: "PTMono";
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.html {
  height: 100%;
  text-align: center;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16.41px;
  font-weight: 400;
  font-style: normal;
  scroll-behavior: smooth;
  margin: 0 auto;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.body {
  height: 100%;
  display: flex;
  flex-flow: column;
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}

a {
  text-decoration: none;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

.preloader {
  position: fixed;
  inset: 0;
  z-index: 1001;
}

.preloader__container {
  position: relative;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  margin-top: -35px;
  margin-left: -35px;
  text-align: center;
  animation: preloader-rotate 2s infinite linear;
}

.preloader__item {
  position: absolute;
  display: inline-block;
  top: 0;
  background-color: skyblue;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  animation: preloader-bounce 2s infinite ease-in-out;
}

.preloader__item:last-child {
  top: auto;
  bottom: 0;
  animation-delay: -1s;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes preloader-bounce {
  0%, 100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
.header {
  max-width: 940px;
  width: 100%;
  margin: 0 auto;
}

.navigation {
  display: flex;
  justify-content: space-between;
  font-size: 18px;
  text-transform: capitalize;
}

.navigation__link {
  color: #828282;
}
.navigation__link :active {
  color: #070707;
  transition-delay: 0.05s;
}

.navigation__line {
  color: #828282;
}

.main {
  max-width: 940px;
  width: 100%;
  margin: 0 auto;
  padding: 0;
}

.naming-project__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
  line-height: 21.83px;
}

.naming-project__title {
  font-size: 47.34px;
  display: block;
  flex-direction: column;
  text-transform: capitalize;
  line-height: 58.61px;
  width: 129px;
}

.naming-project__text-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.naming-project__text-ui {
  margin: 0;
  padding: 0;
  padding-top: 20%;
}

.naming-project__text-minsk {
  margin: 0;
  padding: 1px;
}

.naming-project__lang {
  color: #828282;
  font-size: 16px;
  transform: rotate(-90deg);
  text-transform: capitalize;
  height: 20px;
}

.naming-project__img {
  width: 100%;
  height: 100%;
}

.about-me {
  margin: 224px auto 0 auto;
  padding: 0;
  width: 425px;
}

.about-me__title {
  text-transform: capitalize;
  font-size: 34px;
  line-height: 42.09px;
}

.about-me__text-ui {
  line-height: 21.83px;
  font-size: 18px;
}

.about-me__text-courses {
  line-height: 21.83px;
  font-size: 18px;
}

.about-me__text-implement {
  line-height: 21.83px;
  font-size: 18px;
}

.skils {
  margin-top: 198px;
}

.skils__block {
  display: flex;
  justify-content: space-around;
}