.main {
  max-width: 940px;
  width: 100%;
  margin: 0 auto;
  padding: 0;
}

.naming-project {}

.naming-project__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
  line-height: 21.83px;
}

.naming-project__title {
  font-size: 47.34px;
  display: block;
  flex-direction: column;
  text-transform: capitalize;
  line-height: 58.61px;
  width: 129px;
}

.naming-project__text-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.naming-project__text-ui {
  margin: 0;
  padding: 0;
  padding-top: 20%;
}

.naming-project__text-minsk {
  margin: 0;
  padding: 1px;
}

.naming-project__lang {
  color: #828282;
  font-size: 16px;
  transform: rotate(-90deg);
  text-transform: capitalize;
  height: 20px;
}

.naming-project__img-wrapper {}

.naming-project__img {
  width: 100%;
  height: 100%;
}

.about-me {
  margin: 224px auto 0 auto;
  padding: 0;
  width: 425px;
}

.about-me__title {
  text-transform: capitalize;
  font-size: 34px;
  line-height: 42.09px;
}

.about-me__text-ui {

  line-height: 21.83px;
  font-size: 18px;
}

.about-me__text-courses {
  line-height: 21.83px;
  font-size: 18px;
}

.about-me__text-implement {
  line-height: 21.83px;
  font-size: 18px;
}

.skils {
  margin-top: 198px;
}

.skils__title {}

.skils__text {}

.skils__block {
  display: flex;
  justify-content: space-around;

